// @ is an alias to /src
import HeadBar from '../components/HeadBar.vue';
import SideBar from '../components/SideBar.vue';
export default {
  name: 'AboutView',

  data() {
    return {
      display_flag: true
    };
  },

  components: {
    HeadBar,
    SideBar
  },
  methods: {
    openSideBar() {
      this.display_flag = true;
    },

    closeSideBar() {
      this.display_flag = false;
    }

  },

  mounted() {
    this.display_flag = window.innerWidth > 992;
  }

};